@import "../../scss/variables";
@charset "UTF-8";
.app-sidebar {
	.mCSB_container {
		margin-right: 0px;
	}
	.mCSB_draggerContainer {
		right: -11px;
	}
}
@media (min-width: 768px) {
	.app.sidenav-toggled .app-sidebar__overlay {
		visibility: hidden;
	}
}
@media (max-width: 767px) {
	.app {
		overflow-x: hidden;
		.app-sidebar {
			left: -300px;
		}
		.app-sidebar__overlay {
			visibility: hidden;
		}
		&.sidenav-toggled1 {
			.app-content {
				margin-left: 0;
			}
			.app-sidebar {
				left: 0;
			}
			.app-sidebar__overlay {
				visibility: visible;
			}
		}
		&.sidebar-mini {
			&.sidenav-toggled.sidebar-gone .second-sidemenu, &.sidebar-gone .second-sidemenu, &.sidenav-toggled1.sidebar-gone .second-sidemenu {
				visibility: hidden;
			}
			&.sidebar-gone.sidenav-toggled1.sidenav-toggled4 .second-sidemenu {
				visibility: visible !important;
			}
		}
	}
	.nav-link.nav-link.toggle {
		-webkit-box-ordinal-group: 0;
		-ms-flex-order: -1;
		order: -1;
	}
	.app-sidebar {
		padding-top: 58px !important;
	}
	.app-header {
		.header-brand {
			margin-left: 1rem;
		}
		.nav-link.icon {
			margin: 3px;
			&:last-child {
				padding-right: 0;
			}
		}
	}
}
.app-content {
	min-height: calc(100vh - 50px);
	margin-top: 0x !important;
	margin-bottom: 0 !important;
	-webkit-transition: margin-left 0.3s ease;
	-o-transition: margin-left 0.3s ease;
	transition: margin-left 0.3s ease;
	padding: 0px;
	overflow: hidden;
}
.footer .container {
	width: 100%;
	padding-right: 0.75rem;
	padding-left: 0.75rem;
	margin-right: auto;
	margin-left: auto;
}
@media (min-width: 768px) {
	.app-content {
		margin-left: 255px;
		.container-fluid {
			padding-right: 30px;
			padding-left: 30px;
		}
	}
	.app.sidebar-mini.sidenav-toggled.sidebar-gone.sidenav-toggled1 .app-sidebar__toggle {
		padding-left: 0;
	}
}
@media (max-width: 767px) {
	.app-content {
		margin-top: 21px !important;
		min-width: 100%;
		padding: 30px 0.75rem 0 0.75rem;
	}
	.app-sidebar .app-logo {
		display: none;
	}
	.app-content .header-brand {
		display: block !important;
		margin-right: 0;
	}
}
@media (max-width: 480px) {}
