}
.app-sidebar__toggle {
	font-family: fontAwesome;
	margin-top: 0px;
	line-height: 2.4;
	-webkit-transition: background-color 0.3s ease;
	-o-transition: background-color 0.3s ease;
	transition: background-color 0.3s ease;
	color: $white;
	&:hover {
		color: $white;
	}
	&:before {
		content: "\e907";
		transition: all .25s;
		font-size: 21px;
		font-family: 'feather';
	}
	&:focus, &:hover {
		text-decoration: none;
	}
}
